.win-grid {
	display: grid;
	grid-template-rows: 200px 1fr;
	grid-template-columns: 1fr 1fr;
}

.grid-header {
	grid-row-start: 1;
	grid-column-start: 1;
	grid-row-end: 2;
	grid-column-end: 3;
}

.grid-p1 {
	grid-row-start: 2;
	grid-column-start: 1;
	grid-row-end: 3;
	grid-column-end:2;
}

.grid-p2 {
	grid-row-start: 2;
	grid-column-start: 2;
	grid-row-end: 3;
	grid-column-end:3;
}

.main-container a {
    font-size: 50px;
    margin-bottom: 30px;
}

.input-fields {
    margin-top: 10px;
}

.input-fields > div {
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
}

.input-fields > div > input {
    height: 30px;
    width: 200px;
}
